@import url(https://fonts.googleapis.com/css2?family=Quantico:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Reggae+One&display=swap);
.Header{display:flex;justify-content:center;align-items:center;width:100%;height:10px;padding:20px 0 0 0;background-color:#181818;box-shadow:0 4px 10px black;box-sizing:content-box;font-size:.9rem;text-shadow:3px 4px 3px black}.Header h2{color:#ff48d6;font-size:3.2rem;text-shadow:2px 3px 2px rgba(0,0,0,0.85);transform:translate(-12px, 2px)}

.GameList__item{display:flex;flex-direction:row;justify-content:space-between;margin:10px 0;width:100%;box-shadow:-2px 5px 6px .2px #0000003a;text-align:center;z-index:0}.GameList__item h3{text-shadow:1px 3px 2px #000}.GameList__item img{border-radius:2px;box-shadow:0 0 5px 2px rgba(0,0,0,0.8);height:50px;width:150px;object-fit:cover;opacity:.85}.GameList__item img:hover,.GameList__item img:focus{opacity:1}.GameList__item .GameList__item--naming img:active{-webkit-filter:initial;filter:initial;opacity:1;-webkit-filter:sepia(39%) saturate(523%) hue-rotate(340deg) brightness(85%) contrast(94%);filter:sepia(39%) saturate(523%) hue-rotate(340deg) brightness(85%) contrast(94%)}.GameList__item .ex--price{font-size:.9rem;text-decoration:line-through;color:#484848}.GameList__item .actual--price{margin:-6px 0;font-size:1.6rem}.GameList__item .actual--price::after{content:'€';font-size:.6rem}.GameList__item .GameList__item--naming{text-align:left}.GameList__item .GameList__item--info{display:flex;flex-direction:column;align-self:center;padding:0 10px}.GameList__item .GameList__item--anim{position:relative;left:0;display:flex;align-items:center;width:100%;transform:translate(22px, 26px);opacity:.75}.GameList__item .GameList__item--anim .shape img{width:16px;height:16px;box-shadow:0 0 0;opacity:1}

.GameList{display:flex;flex-direction:column;align-items:center;margin:60px 0;animation:fadeIn 1s}

.Search{top:0;bottom:0;display:flex;flex-direction:column;align-items:center;margin:60px 0;width:100%;animation:.5s fadeIn;z-index:2}.Search input{position:relative;margin:0 10px 0 0;padding:3px 10px;border-radius:3px;background-color:#484848;color:#f1f1f1;font-size:.75rem;z-index:9}.Search button{position:relative;padding:2px 20px;color:white;border-radius:3px;font-weight:bold;font-size:.8rem;opacity:1;transition:all 1s;animation:changeColorBtn 4s ease-in-out infinite alternate;z-index:9}.Search button:hover{opacity:.75}.Search button:focus,.Search button:active{box-shadow:inset 0 0px 6px #181818}.Search .GameList{margin:20px 0;animation:fadeIn 1s}.Search .GameList__item--anim{display:none}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}

.Loader{position:fixed;top:0;left:0;width:100%;height:100vh;background:#181818dc;z-index:1}.Loader .Loader__anim{display:flex;justify-content:center;align-items:center;width:100%;height:100%}.Loader .Loader__anim h2{color:#ff48d6;font-size:2rem;letter-spacing:5px;transform:translateY(-50px);animation:fadeIn .35s ease-in-out infinite alternate}@media (min-width: 676px){.Loader__anim h2{font-size:2.5rem}}

.Footer{position:fixed;left:0;bottom:-1px;display:flex;justify-content:space-around;align-items:center;width:100%;height:40px;background:linear-gradient(90deg, #CC35E8 0%, #ab3bff 100%);box-shadow:0 -3px 10px black;z-index:1}.Footer .Footer--text{font-size:.6rem;color:#f1f1f1}.Footer .Footer--status{display:flex;align-items:center}.Footer .Footer--status p:nth-child(1){color:#f1f1f1;font-weight:bold;font-size:.7rem;text-shadow:2px 0 2px rgba(0,0,0,0.75);text-decoration:underline;transform:translate(2px, 0)}.Footer .Footer--status p:nth-child(2){color:#f1f1f1;font-size:1.8rem;font-weight:bold;text-shadow:2px 0 2px rgba(0,0,0,0.75);transform:translate(6px, -4px)}

.navButtonLeft,.navButtonRight{position:fixed;top:calc(50vh - 50px);right:-40px;width:80px;height:70px;margin:0;padding-right:35px;padding-bottom:8px;font-size:1.6rem;font-weight:lighter;font-family:'Reggae One', cursive;border-radius:40px;background:rgba(37,37,37,0.856);color:#ff48d6;opacity:.75}.navButtonLeft:hover,.navButtonRight:hover{opacity:1}.navButtonLeft:focus,.navButtonLeft:active,.navButtonRight:focus,.navButtonRight:active{box-shadow:inset 0 0px 6px #181818}.navButtonLeft{left:-40px;padding-right:18px;padding-bottom:8px;text-align:right}.searchExpanderBtn{position:fixed;top:50px;right:-16px;padding:0 10px 0 0;width:60px;height:45px;background-color:#484848;border-radius:35px;color:#f1f1f1;font-size:1rem;opacity:.75;z-index:3}.searchExpanderBtn:hover{opacity:1}.searchExpanderBtn:active{box-shadow:inset 0 0px 6px #181818}

.SplashScreen{position:fixed;top:0;right:0;bottom:0;left:0;display:flex;flex-direction:column;align-items:center;justify-content:center;padding:0 20px;background:#181818;text-align:center;opacity:1;transition:all .35s;z-index:4}.SplashScreen>*{margin:20px 0}.SplashScreen hr{margin:7.5px 0 10px;border-bottom:1px solid #ff48d6}.SplashScreen strong{font-size:.85rem;animation:changeColor .5s ease-in-out infinite alternate}.SplashScreen button{padding:5px 26px;color:white;border-radius:6px;font-weight:bold;font-size:1rem;cursor:pointer;opacity:1;transition:all 1s;animation:changeColorBtn 4s ease-in-out infinite alternate}.SplashScreen button:hover{opacity:.65}.SplashScreen button:focus,.SplashScreen button:active{box-shadow:inset 0 0px 6px #181818}.SplashScreen .SplashScreen--way{position:absolute;top:35px;padding:0 10px;margin:0 20px;border-right:1px solid #CC35E8}.SplashScreen .SplashScreen--way p{font-size:.8rem;color:rgba(255,255,255,0.75)}.SplashScreen .SplashScreen--way em{font-size:.75rem;opacity:.85}.SplashScreen .SplashScreen--title{margin:30px 0;width:300px;background:white;color:#181818;font-size:1.8rem;text-transform:uppercase}.SplashScreen .SplashScreen--title p{font-size:.85rem;padding:0 50px}@keyframes changeColor{from{color:#ff48d6}to{color:#ab3bff}}@keyframes changeColorBtn{from{background:#CC35E8}to{background:#ff48d6}}

*{margin:0;padding:0;color:#f1f1f1;font-family:'Quantico', sans-serif;font-size:16px;border:none;list-style-type:none;outline:none;scroll-behavior:smooth;-webkit-user-select:none;-ms-user-select:none;user-select:none}body{background:#181818}body a{color:#ff48d6;font-size:.8rem}body button{cursor:pointer}body em{color:#ff48d6}.App{display:flex;flex-direction:column;justify-content:center;align-items:center}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@media (min-width: 676px){.GameList{justify-content:center;flex-direction:row;flex-wrap:wrap}.GameList .GameList__item{display:flex;flex-direction:row;margin:10px 60px;width:250px;box-shadow:-2px 5px 6px .2px #0000003a;text-align:center}.GameList .GameList__item .GameList__item--anim{position:relative;left:0;display:flex;align-items:center;width:100%;transform:translate(20px, 25px);opacity:.75}}

