@import '../variables';

.SplashScreen {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  padding: 0 20px;

  background: $dark-color;
  text-align: center;

  opacity: 1;
  transition: all .35s;
  z-index: 4;

  > * {
    margin: 20px 0; }

  hr {
    margin: 7.5px 0 10px;
    border-bottom: 1px solid $p-color; }

  strong {
    font-size: .85rem;
    animation: changeColor .5s ease-in-out infinite alternate; }

  button {
    padding: 5px 26px;

    color: white;
    border-radius: 6px;

    font-weight: bold;
    font-size: 1rem;

    cursor: pointer;
    opacity: 1;

    transition: all 1s;
    animation: changeColorBtn 4s ease-in-out infinite alternate;

    &:hover {
      opacity: .65; }
    &:focus,
    &:active {
      box-shadow: inset 0 0px 6px $dark-color; } }


  .SplashScreen--way {
    position: absolute;
    top: 35px;

    padding: 0 10px;
    margin: 0 20px;

    border-right: 1px solid $t-color;

    p {
      font-size: .8rem;
      color: rgba(255, 255, 255, .75); }

    em {
      font-size: .75rem;
      opacity: .85; } }


  .SplashScreen--title {
    margin: 30px 0;
    width: 300px;

    background: white;
    color: $dark-color;

    font-size: 1.8rem;
    text-transform: uppercase;

    p {
      font-size: .85rem;
      padding: 0 50px; } } }


@keyframes changeColor {
  from {
    color: $p-color; }
  to {
    color: $s-color; } }

@keyframes changeColorBtn {
  from {
    background: $t-color; }
  to {
    background: $p-color; } }
