@import '../variables';

.Header {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 100%;
  height: 10px;

  padding: 20px 0 0 0;
  background-color: $dark-color;

  box-shadow: 0 4px 10px black;
  box-sizing: content-box;

  h3 {}
  font-size: .9rem;
  text-shadow: 3px 4px 3px black;

  h2 {
    color: $p-color;

    font-size: 3.2rem;
    text-shadow: 2px 3px 2px rgba(0, 0, 0, 0.85);

    transform: translate(-12px, 2px); } }
